body {
  margin: 0;
  font-family: 'Segoe UI', 'Noto Sans TC', sans-serif;
  background: linear-gradient(to right, #f0f4f8, #e0f7fa);
  color: #333;
}

.container {
  max-width: 800px;
  margin: 2rem auto;
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #0f172a;
}

.tabs {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.tabs button {
  padding: 0.5rem 1rem;
  border: none;
  background: #e2e8f0;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
}

.tabs button.active {
  background: #3B82F6;
  color: white;
}

.chart-area {
  width: 100%;
  height: 300px;
}